  
<script setup>
import { __ } from '@wordpress/i18n';
import { ref, defineProps, computed, onMounted, defineEmits } from 'vue';
import Icon from '@components/Icon.vue';

const props = defineProps({
    modelValue: {
        type: Boolean,
        default: false,
    },
    mail: {
        type: String,
        default: 'support@wppool.dev',
    },
})

// handle the modal 
const isOpen = ref(props.modelValue)

const emit = defineEmits(['update:modelValue'])
const close = () => {
    isOpen.value = false
    emit('update:modelValue', false)
}

// Generate links for mailing.
const Link = computed(() => {

    const mail = props.mail.replace('@', '%40');
    const subject = 'WP Dark Mode Support';
    const body = 'Hi, I need help with WP Dark Mode.';

    return {
        Gmail: `https://mail.google.com/mail/?view=cm&fs=1&to=${mail}&su=${subject}&body=${body}`,
        Outlook: `https://outlook.office.com/owa/?path=/mail/action/compose&to=${mail}&subject=${subject}&body=${body}`,
        Yahoo: `https://compose.mail.yahoo.com/?to=${mail}&subject=${subject}&body=${body}`,
        Mailto: `mailto:${mail}?subject=${subject}&body=${body}`,
    };
});


// Copy function 
const isCopied = ref(false)
const copyEmail = async () => {
    isCopied.value = true;
    await navigator.clipboard.writeText(props.mail);
    setTimeout(() => {
        isCopied.value = false;
    }, 3000);
};


onMounted(() => {
    setTimeout(() => {
        const el = document.querySelector('.modal')
        el && el.focus()
    }, 100)
})

</script>

