<script setup>
import { Card, Toggle, Hints, Heading, Suggestions, DarkModeSwitch, Alert, Button, Icon } from '@components'
import DarkModeStore from '@o/store'
const { options } = DarkModeStore()
import { getRange } from '@o/helper'

// setFromRange.
const setFromRange = (n, key) => {
    options[key] = n
}
</script>

<template>
    <Card borderless transparent class="gap-5">

        <Alert color="yellow" v-if="!options.frontend_enabled"> {{__('The Content Switch is <strong>currently unavailable</strong>. To access this feature, first you have to enable the “Frontend Dark Mode” feature from the Control settings', 'wp-dark-mode')}}
            <template #button>
                <RouterLink to="/frontend"><Button color="yellow" class="text-xs whitespace-nowrap bg-[#F59E0B] border-[#F59E0B]">
                    {{__('Enable Frontend Dark Mode', 'wp-dark-mode')}} <Icon name="chevronRight" /></Button></RouterLink>
            </template>
        </Alert>


        <Card class="gap-6" :blur="!options.frontend_enabled" locked>
            <div class="flex flex-col gap-3 w-fit">
                <Toggle v-model="options.content_switch_enabled_top_of_posts" locked :disabled="!options.frontend_enabled">
                     {{__('Display dark mode switch at top of all posts', 'wp-dark-mode')}}</Toggle>
                <Hints locked>{{__('Enable to display the dark mode switch button in menus.', 'wp-dark-mode')}}</Hints>
            </div>
            <div class="flex flex-col gap-3  w-fit">
                <Toggle v-model="options.content_switch_enabled_top_of_pages" locked :disabled="!options.frontend_enabled">
                    {{__('Display dark mode switch at top of all pages', 'wp-dark-mode')}}</Toggle>
                <Hints locked>{{__('Enable to display the dark mode switch button in menus.', 'wp-dark-mode')}}</Hints>
            </div>
        </Card>

        <Card  :blur="!options.frontend_enabled" v-if="options.content_switch_enabled_top_of_posts || options.content_switch_enabled_top_of_pages">
            <div class="flex flex-col gap-2">
                <Heading class="mb-1">{{__('Select Switches', 'wp-dark-mode')}}</Heading>
                <div class="flex flex-wrap gap-3">
                    <div v-for="n in getRange(1, 13)" :key="n"
                        :class="{ 'ring ring-blue-400': options.content_switch_style === n, }"
                        @click.prevent="setFromRange(n, 'content_switch_style');"
                        class="flex flex-col items-center justify-center transition duration-75 bg-gray-100 rounded-lg cursor-pointer w-36 h-36">
                    <DarkModeSwitch :id="n"/>
                    </div>
                </div>
            </div>
        </Card>
        
        <Suggestions paths="shortcode, excludes, accessibility"></Suggestions>
    </Card>
</template>