<script setup>
import { reactive } from 'vue';
import { useRoute, useRouter } from 'vue-router';
const route = useRoute();
const router = useRouter();

const activeClass = ( _route ) => {
    return route.path === _route ? 'active' : '';
};

const state = reactive( {
    support: false
} );

const navs = [
    '/',
    '/gutenberg',
    '/elementor',
    '/faq',
    '/compare'
];

const next = () => {
    const index = navs.indexOf( route.path );
    if ( index === navs.length - 1 ) {
        return;
    }
    router.push( navs[ index + 1 ] );
};

const prev = () => {
    const index = navs.indexOf( route.path );
    if ( index === 0 ) {
        return;
    }
    router.push( navs[ index - 1 ] );
};

import Mailto from '@/Mailto.vue';

const OpenSupport = () => {
    if( ! wp_dark_mode_admin_json.is_ultimate ) {
        window.open( 'https://wordpress.org/support/plugin/wp-dark-mode/', '_blank' );
    } else {
        state.support = true;
    }
};
</script>

