import { SunOutlined, StarMoonOutlined, DoubleT } from './SVG'; export default (config = {}) => { return ` <div class="_scheme wp-dark-mode-ignore"> <div class="_icon wp-dark-mode-ignore">${ config.iconLight || SunOutlined }</div> <div class="_icon wp-dark-mode-ignore">${ config.iconDark || StarMoonOutlined }</div> </div> <div class="_font wp-dark-mode-ignore"> <div class="_icon wp-dark-mode-ignore">${config.fontIcon ? `<img src="${config.fontIcon}"></img>` : DoubleT}</div> <div class="_icon wp-dark-mode-ignore">${config.fontIconActive ? `<img src="${config.fontIconActive}"></img>` : DoubleT}</div> </div>` }